.node {
  height: 28px;
  width: 28px;
  border: 0.1px solid rgb(188, 188, 255);
  border-left: none;
  border-top: none;
  cursor: pointer;
  overflow: hidden;
}
.node:nth-child(1) {
  border-top: 0.1px solid rgb(188, 188, 255);
}
.wall {
  animation: plotWall 0.2s ease;
  background-color: rgb(67, 64, 77);
}

@keyframes plotWall {
  0% {
    transform: scale(0.5);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

.startNode {
  background-color: green;
}

.finishNode {
  background-color: red;
}
