.navbar {
  position: relative;
  width: 100%;
  background-color: #34495e;
  color: white;
  font-size: 18px;
}
.title {
  font-size: 24px;
}
.buttons {
  margin-left: 10px;
  display: flex;
  flex-direction: row;
  color: white;
  font-size: 1em;
}
.list {
  color: rgb(216, 216, 216);
  margin-left: 10px;
  margin-right: 10px;
}

.list:hover {
  cursor: pointer;
  color: white;
}

#nav-dropdown {
  color: rgb(216, 216, 216);
}

#nav-dropdown:hover {
  color: white;
}

.disabled {
  cursor: not-allowed;
  font: small;
  color: grey;
  margin-left: 10px;
}

.disabled:hover {
  color: #aaa;
}
